.skills_list,.other_Skills{
    h5{
        margin-bottom:20px;
        font-size: 20px;
        text-transform:capitalize;
    }
    .padding_div{
        position: relative;
        padding:10px 15px;
        padding-bottom:35px;
        span{
            position: absolute;
            left: 15px;
            color: #a4acc4;
            -webkit-text-fill-color: unset!important;
            top: 42px;
            font-size: 21px;
        }
        .MuiGrid-root{
            padding-left:55px;
            .MuiLinearProgress-barColorPrimary {
                background-color: #037fff;
            }
            .MuiLinearProgress-colorPrimary{
                background-color: #2e344e; 
            }
            .MuiLinearProgress-root{
                height: 6px;
            }
        }
    }
}
.other_Skills,.workExp{
    padding-top:40px;
    margin-bottom:25px;
    
    .pad_left{padding-left:50px;} 
}
.main_border{
    border-left: 3px solid #2e344e;
    .m-t-25{margin-top:25px;}
    .title_content{
        position: relative;
        h3{
            padding-left: 20px;
            font-size:18px;
            color: #a4acc4;
        }
        &:before{
            content: "";
            position: absolute;
            left: -9px;
            top: 6px;
            height: 15px;
            width: 15px;
            border-radius: 100px;
            background: #10121b;
            border: 3px solid #2e344e;
        }
    }
    .right_side{
        padding-left:50px;
        position: relative;
        &:after{
            content: "";
            position: absolute;
            left: 0;
            top: 15px;
            height: 1px;
            width: 30px;
            background: #2e344e;
        }
        h3{
            color: #037fff;
            margin-bottom: 0;
            font-weight: 700;
            font-size:1.43rem;
            line-height: 2.14rem;
        }
        h4{
            margin-top:10px;
            margin-bottom:15px;
            color:#fff;
            font-size:18px;
        }
        p{
            color: #a4acc4;
            font-size:16px;
        }
    }

}
.resume_page.page_top {
    .title{
        margin-left:45px;
        &:after{
            z-index:99;
        }
        &:before{
            z-index: 99;
        }
    }
  
    margin-left: 45px;
    .water_mark{
        position: absolute;
    left: 0;
    top: 100%;
    font-size: 42px;
    line-height: 1;
    font-weight: 900;
    color: rgba(25, 29, 43, 0.44);
    display: inline-block;
    text-transform: uppercase;
    z-index: -1;
    transform: translateY(-40%);
    -webkit-user-select: none;
    user-select: none;
    white-space: nowrap;
    z-index: 9;
    background: unset!important;
    -webkit-background-clip: unset!important;
    -webkit-text-fill-color: unset!important;
    }
}

@media(max-width:768px){
    .resume_page.page_top{
        margin-left: 0px;
        .title{
            margin-left: 0px;
            span{
                white-space: unset;
                font-size:34px;
            }
        }
    }
    .skills_list{
        .MuiContainer-maxWidthLg{
            padding:0px;
        }
        .padding_div{
            padding-left:0px;
            span{
                left: 0px;
                font-size: 16px;
            }
            h5{
                font-size: 18px;
            }
            .MuiGrid-root{
                margin-top: -7px;
            }
            
        }
    }
    .other_Skills{
        .padding_div{
            padding-left:0px;
            span{
                left: 0px;
                font-size: 16px;
            } 
            h5{
                font-size: 18px;
            }
            .MuiGrid-root{
                margin-top: -7px;
            }
        }
    }
    .main_border{
        .right_side{
            padding-left: 20px;
            &:after{
                position: absolute;
                left: 2px;
                top: -14px;
                height: 1px;
                width: 15px;
            }
        }
    }
}