
@import 'Theme';
.contact_page.page_top{
    .grid_content{
        padding-left:70px;
        position: relative;
        z-index: 999;
        padding-bottom:50px;
        h3{
            font-size: 1.71rem;
            line-height: 2.43rem;
            padding-bottom: 20px;
        }
        h2{
            .water_mark{
            position: absolute;
            left: 0;
            top: 100%;
            font-size: 52px;
            line-height: 1;
            font-weight: 900;
            color: rgba(25, 29, 43, 0.44);
            display: inline-block;
            text-transform: uppercase;
            z-index: -1;
            transform: translateY(-40%);
            -webkit-user-select: none;
            user-select: none;
            white-space: nowrap;
            z-index: 9;
            background: unset!important;
            -webkit-background-clip: unset!important;
            -webkit-text-fill-color: unset!important;
            }
        }
        .input_field{
            position: relative;
            z-index:99;
            padding:15px;
            padding-left:0px;
            textarea{
                color: #a4acc4;
            }
            input{
                color: #a4acc4;
            }
            fieldset{
                border: 1px solid #2e344e!important;
                border-radius:0px;
                .span{
                    color: #a4acc4;
                    background:unset;
      
                }
            }
            label{
                color: #a4acc4;
            }
            .submit_btn{
                position: relative;
                padding: 0 30px;
                background: $btn-background-color;
                border: 0px;
                display: inline-block;
                z-index: 1;
                text-transform: uppercase;
                font-size: .9rem;
                letter-spacing: 2px;
                height: 50px;
                line-height: 50px;
                color:$white-color;
                border-radius:0px;
                font-weight:600;
                -webkit-transition: all .4s ease-out;
                transition: all .4s ease-out;   
                span{
                    
                    background: -webkit-linear-gradient(white, #ffffff)!important;
                    -webkit-background-clip: text!important;
                }
                &:hover::before {
                    transform: scaleX(1);
                    -webkit-transition: all .4s ease-out;
                    transition: all .4s ease-out;   
                  }
                
                &:before{
                  
                    content:"";
                    position: absolute;
                    left: 0;
                    top: auto;
                    bottom: 0;
                    height: 2px;
                    width: 100%;
                    background: $white-color;
                    z-index: -1;
                    -webkit-transform: scaleX(0);
                    transform: scaleX(0);
                    -webkit-transform-origin: 0 0;
                    transform-origin: 0 0;
                }
             
            }
          }
          .info{
            position: relative;
            z-index:99;
            padding:15px;
            padding-right:0px;
            .infoblock{
                margin-top: 30px;
                background: #191d2b;
                padding: 30px;
                display: flex;
                span{
                    display: inline-block;
                    height: 70px;
                    width: 70px;
                    flex: 0 0 70px;
                    max-width: 70px;
                    border: 1px solid #2e344e;
                    text-align: center;
                    line-height: 66px;
                    margin-right: 20px;
                    svg{
                        height: 30px;
                        width: 30px;
                        vertical-align: middle;
                    
                    }
                }
                .content{
                    h6{
                        font-size: 1.14rem;
                        line-height: 1.857rem;
                        font-family: Nunito,sans-serif;
                        color: $white-color;
                    }
                    p{
                        margin-top:8px;
                        color:$text-color ;
                    }
                }
            }
          }
          
    }
}

@media(max-width:768px){
    .contact_page.page_top{
        .grid_content{
            padding-left:0px;
            padding-top: 90px;
            .title{
                margin-bottom: 15px;
                font-size: 36px;
                font-family: $default-font;
            }
            h2{
                .water_mark{
                    font-size: 36px;

                }
            }
            .info{
                padding-left:0px!important;
                .infoblock{
                    display: block;
                    padding: 20px;
                    span{
                        margin-right:0px;
                        margin-bottom: 20px;
                        height: 50px;
                        width: 50px;    
                        line-height: 50px;
                    }
                }
            }
            .input_field{
                padding-right: 0px;
                textarea{
                    color: #a4acc4;
                }
            }
            .no-pad-right{
                padding:0px;
            }
        }
    }
}

.Toastify__toast--default{
    font-size:14px!important;
    font-weight:normal;
    z-index:9999;
    letter-spacing:normal;
}
#map {
    width:100%;
    height: 400px;
  }