
@import 'Theme';
.pad_enable{
    padding:15px;
    margin-bottom:10px!important;
}
.about_page{
    margin-top: 60px;
    .grid_content{
        padding-top:120px;
        padding-left: 75px;

        .title{
            font-weight: 700;
            position: relative;
            padding-bottom: 15px;
            text-transform: uppercase;
            font-size: 2.57rem;
            line-height: 3.2857rem;
            font-family: 'Nunito Sans', sans-serif;
            margin-bottom: 60px;
            .water_mark{
            position: absolute;
            left: 0;
            top: 100%;
            font-size: 52px;
            line-height: 1;
            font-weight: 900;
            color: rgba(25, 29, 43, 0.44);
            display: inline-block;
            text-transform: uppercase;
            z-index: -1;
            transform: translateY(-40%);
            -webkit-user-select: none;
            user-select: none;
            white-space: nowrap;
            z-index: 9;
            background: unset!important;
            -webkit-background-clip: unset!important;
            -webkit-text-fill-color: unset!important;
            }
    &:before {
        content: "";
        position: absolute;
        left: 0;
        top: auto;
        bottom: 0;
        height: 5px;
        border-radius: 100px;
        background: rgba(3,127,255,.3);
        width: 100px;
        z-index: 999;
    }
    &:after {
        content: "";
        position: absolute;
        left: 0;
        top: auto;
        bottom: 0;
        height: 5px;
        border-radius: 100px;
        width: 35px;
        background: #037fff;
        z-index: 999;
    }
        }
    }
    .about-image{
        width:100%;
        height:400px;
        background-position: 100% 30%;
        background-size: cover;
        background-repeat: no-repeat;
        position: relative;
        z-index:9;
        &:after{
            content: "";
            position: absolute;
            height: 55%;
            width: 15px;
            background: rgba(3,127,255,.6);
        }
        &:before{
            content: "";
            position: absolute;
            height: 55%;
            width: 15px;
            background: rgba(3,127,255,.6);
            right: 0;
            bottom: 0;
        }
    }
    .about-content{
        padding-left: 30px;
        h3{
            font-size: 2rem;
            font-family: 'Nunito', sans-serif;
            line-height: 2.7rem;
            font-weight: 600;
            margin-top: -8px;
            .color-theme {
                color: #037fff;
                -webkit-text-fill-color: unset!important;
            }
        }
        p{
            color: #a4acc4;
            font-size: 18px;
            font-family: 'Nunito', sans-serif;
            line-height: 1.8rem;
        }
         ul{
            li:not(:last-child) {
                margin-bottom: 3px;
            }
            li{
                font-size: 18px;
                font-family: 'Nunito', sans-serif;
                letter-spacing: normal;
                color: #a4acc4;
                padding-left:0px;
                padding-top:5px;
                padding-bottom:5px;
                b {
                    min-width: 120px;
                    display: inline-block;
                    position: relative;
                    margin-right: 7px;
                    &:after {
                        content: ":";
                        position: absolute;
                        top: 0;
                        left: auto;
                        right: 0;
                    }
                }
            }
         }
         .dnl_cv_btn{
            position: relative;
            padding: 0 30px;
            background: $btn-background-color;
            border: 0px;
            display: inline-block;
            z-index: 1;
            text-transform: uppercase;
            font-size: .9rem;
            letter-spacing: 2px;
            height: 50px;
            line-height: 50px;
            color:$white-color;
            border-radius:0px;
            font-weight:600;
            -webkit-transition: all .4s ease-out;
            transition: all .4s ease-out;   
            &:hover{
                text-decoration:unset;
            }
            span{
                    
                background: -webkit-linear-gradient(white, #ffffff)!important;
                -webkit-background-clip: text!important;
            }
            &:hover::before {
                transform: scaleX(1);
                -webkit-transition: all .4s ease-out;
                transition: all .4s ease-out;   
              }
            
            &:before{
              
                content:"";
                position: absolute;
                left: 0;
                top: auto;
                bottom: 0;
                height: 2px;
                width: 100%;
                background: $white-color;
                z-index: -1;
                -webkit-transform: scaleX(0);
                transform: scaleX(0);
                -webkit-transform-origin: 0 0;
                transform-origin: 0 0;
            }
         }
    }
    .sceond_section{
        padding-top: 120px;
        padding-bottom:80px;
        .service{
            border: 1px solid #2e344e;
            border-top: 5px solid #2e344e;
            padding: 30px;
            background: #191d2b;
            -webkit-transition: all .4s ease-out;
            transition: all .4s ease-out;
            height:280px;
            h5{
                font-weight: 600;
                position: relative;
                padding-bottom: 15px;
                margin-bottom: 15px;
                font-size: 1.43rem;
                font-family: 'Nunito', sans-serif;
                line-height: 2.14rem;
                letter-spacing:normal;
                margin-top:15px;
                &:after{
                    content: "";
                    position: absolute;
                    left: 0;
                    top: auto;
                    bottom: 0;
                    height: 2px;
                    width: 50px;
                    background: #2e344e;
                }
            }
        }
    }
}
@media(max-width:768px){
    .pad_enable{
        padding: 0;
        padding-bottom: 20px;
    }
    .about_page{
        .grid_content{
            padding-top: 90px;
            padding-left:0px;
            .title{
                margin-bottom: 35px;
                font-size: 36px;
                font-family: 'Nunito', sans-serif;
                .water_mark{
                    font-size: 48px;
                }
            }
        } 
        .about-image{
            height: 280px;
            margin-bottom:40px;
        }
        .about-content{
            padding-left: 0px;
            p{
                text-align: justify;
                font-size: 17px;
                font-family: 'Nunito', sans-serif;
                line-height:23px;
                margin-top: 10px;
                margin-bottom: 13px;
            }
            ul{
                li{
                        font-size: 15px;
                        font-family: 'Nunito', sans-serif;
                        padding-bottom: 3px;
                        padding-top: 3px;
                        padding-right: 0;
                }
            } 
        }
        .sceond_section{
            padding-top: 30px;
            padding-bottom: 30px;
        }
    } 
   
}