.project_page.page_top{
    .grid_content{
        padding-left:75px;
        position: relative;
        z-index: 999;
        h2{
            .water_mark{
                position: absolute;
            left: 0;
            top: 100%;
            font-size: 52px;
            line-height: 1;
            font-weight: 900;
            color: rgba(25, 29, 43, 0.44);
            display: inline-block;
            text-transform: uppercase;
            z-index: -1;
            transform: translateY(-40%);
            -webkit-user-select: none;
            user-select: none;
            white-space: nowrap;
            z-index: 9;
            background: unset!important;
            -webkit-background-clip: unset!important;
            -webkit-text-fill-color: unset!important;
            }
        }
        .border_bx{
            padding: 15px;
            border: 4px solid #2e344e;
            position: relative;
           
            .default_icon{
                -webkit-transform: translateY(-50%);
                transform: translateY(-50%);
                visibility: hidden;
                opacity: 0;
                margin-top: 30px;
                -webkit-transition: all .4s ease-in-out .2s;
                transition: all .4s ease-in-out .2s;
                svg{
                    position: absolute;
                    left: 0px;
                    right: 0px;
                    margin: 0 auto;
                    z-index: 999;
                    color: #007bff;
                    background-color: #fefefe;
                    top: 200px;
                    border-radius: 50%;
                    height: 45px;
                    width: 45px;
                    padding: 9px 3px;
                    cursor: pointer;
                }
              
            }
            .prj-image{
                width:100%;
                background-position:100% 0%;
                height:450px;
                background-size:cover;
                position: relative;
                cursor: pointer;
                &:hover {
                   
                        .default_icon{
                            
                            visibility: visible;
                            opacity: 1;
                            margin-top: 0;
                        }
                     
                    &::before{
                        -webkit-transform: scaleX(1);
                        transform: scaleX(1);
                        
                    }
                }
                &:before{
                    content:"";
                    position: absolute;
                    background-color:rgba(0, 123, 255, 0.26);
                    z-index:99;
                    height: calc(100% - 30px);
                    width: calc(100% - 30px);  
                    top:15px;               
                    left:15px;

                    -webkit-transition: all .4s ease-out;
                    transition: all .4s ease-out;
                    -webkit-transform: scaleX(0);
                    transform: scaleX(0);
                    -webkit-transform-origin: 0;
                    transform-origin: 0;
                   
                        
        
                   
                }
            }
            
                img.images{
                    width:100%;
                }
                
            
        }
  
      
        .padd{
            padding:20px;
            
        }
        .no-pad-left{
            padding-left:0px!important;
        }
        .no-pad-right{
            padding-right:0px;
        }

        .content{
            margin-top: 25px;
            h3{
                color: #037fff;
                margin-bottom:10px;
                font-weight: 700;
                font-size: 1.43rem;
                line-height: 2.14rem;
            }
            a{
                padding-bottom:15px;
                color: #fff;
                font-size: 18px!important;
                cursor:pointer;
                letter-spacing: normal;
                display: block;
               
            }
            p{
                color: #a4acc4;
                font-size: 15px;
                text-align: justify;
                span.desc{
                    -webkit-text-fill-color: unset!important;
                    color:#fff;
                }
            }
            .first_role{
                margin-bottom:15px;
            }
        }
    }
   
}

@media(max-width:768px){
    .project_page.page_top{
        .grid_content{
            padding-left:0px;
            padding-bottom:50px;
            padding-top: 80px;
            h2{

                .water_mark{
                    
                    font-size: 36px;
                }
            }
            .border_bx{
                .prj-image{
                    height:230px;
                }
            }
            .no-pad-left{
                padding:0px!important;
            }
            .no-pad-right{
                padding:0px;
            }
            .content{
                padding-bottom:25px;
            }
            .title{
                margin-bottom: 40px;
            }
        }
    }
    .default_icon{
        svg{
            top: 100px!important;
        }
    }
}

