@import url('https://fonts.googleapis.com/css2?family=Nunito&display=swap');
.displ_new{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    overflow: hidden;
}
.sideMenu{
    // padding-top: 70px!important;
    width:100%;
    li{
        padding:0;
        &:hover{
            background: rgba(3,127,255,.2);
            color: #037fff;
            -webkit-transition: all .4s ease-out;
            transition: all .8s ease-out;

        }
        a{
            color: #a4acc4;
            text-decoration: unset;
            font-size: 16.5px!important;
            text-align:center;
            cursor: pointer;      
            display:block;
            font-size: 1.1rem;
            letter-spacing: 1px;
            color: #a4acc4;
            font-weight: 500;
            width:100%;
            padding: 10px 15px;
            text-transform:uppercase;
            font-family: 'Nunito', sans-serif;
            -webkit-transition: all .4s ease-out;
            transition: all .4s ease-out;
            padding-top:13px;
            &:hover::before {
                background: rgba(3,127,255,.2);
                color: #037fff;
                // transform: scaleX(1);
                // -webkit-transition: all .4s ease-out;
                // transition: all .4s ease-out;   
                width: 100%;
                visibility: visible;
            }
            &:before{
                content:"";
                position: absolute;
                top: 0;
                left: 0;
                right: 100%;
                height: 100%;
                width: 0;
                background: rgba(3,127,255,.2);
                visibility: hidden;
                z-index: -1; 
                -webkit-transition: all .4s ease-out;
                transition: all .4s ease-out;
               
                    
            }
            &.active{
                color:#fff;
                background: #007bff;
                font-weight:600;
            }
        }
    
    }

    
}
.card_prf{
    background-color: #191d2b!important;
    border-bottom: 1px solid #2e344e;
    padding: 30px 0;
    .profile_img{
        height: 200px;
        width: 200px;
        border-radius: 1000px;
        border: 7px solid #2e344e;
        overflow: hidden;
        display: inline-block;
        vertical-align: middle;
    }
}

nav{
    .MuiDrawer-paper{
        background-color: #191d2b;
        border-right: 1px solid #2e344e;
    }
}
.copyright{
    position: unset;
    width: 100%;
    bottom: 10px;
    padding: 20px 0px 10px;
    font-size: 18px;
    color: #a4acc4;
    cursor: pointer;
    border-top: 1px solid #2e344e;
}
.img_sec{
width:100%;
}
@media only screen and (min-width: 768px) {
    .mobile-resposive{
        display: none;
    }
}
@media(max-width:768px){
    .MuiDrawer-root .MuiDrawer-paperAnchorLeft,.jss109{
        width:250px!important;
    }
    .card_prf {
        text-align: center;
    }
    
    .MuiDrawer-paperAnchorLeft{
        background-color: #191d2b!important;
    }
    .MuiAppBar-colorPrimary{
        background-color: #191d2b!important;
    }
    .sideMenu{
        padding-top:40px!important;
        padding-bottom: 50px!important;
    }
    .copyright{
        bottom: -160px;
        text-align:center;
    }
    .displ_new{
        overflow:auto;
        display: block;
    }
    
   
}