@import url('https://fonts.googleapis.com/css2?family=Nunito&display=swap');
body{
    font-family: 'Nunito', sans-serif;
}
.page_top{
margin-top: 60px;
.grid_content{
    padding-top:120px;
    .title{
        font-weight: 700;
        position: relative;
        padding-bottom: 15px;
        text-transform: uppercase;
        font-size: 32px;
        line-height: 3.2857rem;
        font-family: 'Nunito Sans', sans-serif;
        margin-bottom: 60px;
        &:before {
            content: "";
            position: absolute;
            left: 0;
            top: auto;
            bottom: 0;
            height: 5px;
            border-radius: 100px;
            background: rgba(3,127,255,.3);
            width: 100px;
            z-index:999;
        }
        &:after {
            content: "";
            position: absolute;
            left: 0;
            top: auto;
            bottom: 0;
            height: 5px;
            border-radius: 100px;
            width: 35px;
            background: #037fff;
            z-index:999;
        }
    }
}
}

.mi-bglines{
    position: absolute;
    top: 0;
    height: 100%;
    min-height: 100vh;
    z-index: 0;
    left: 300px;
    width: calc(100% - 300px);
    span{
        display: inline-block;
        left: 0;
        width: 1px;
        background:rgba(46, 52, 78, 0.30196078431372547)!important;
        position: fixed;
        top: 0;
        height: 100%;
        min-height: 100vh;
        &:first-child{
            left: 10%;
        }
        &:nth-child(2){
            left: 40%;
        }
        &:nth-child(3){
            left: 60%;
        }
        &:nth-child(4){
            left: 80%;
        }
        // &:nth-child(5){
        //     left: 98%;
        // }
      
    }
 
}
@media(max-width:768px){
    .mi-bglines span:nth-child(2) {
        left: 35%;
    }
    .mi-bglines span:nth-child(4) {
        left: 84%;
    }
}