
.home_section{
 
    position: fixed;
    top: 40%;
    width: 85%;
    div{
        text-align: center;
        h1{
            font-size: 3.2857rem;
            line-height: 4rem;
            font-family: 'Nunito', sans-serif;
            
    text-align: center;
            .color-theme {
                color: #037fff;
                background: unset !important;
                -webkit-text-fill-color: #037fff !important;
                margin-left:40px;
            }
        }
        p{
            margin-top: 15px;
            font-size: 1.25rem;
            line-height: 2rem;
            color: #a4acc4;
            font-weight: 400;
            margin-bottom: 15px;
        }
        a{
            color:#fff;
            margin-top:25px;
        }
    }
    
}

::-webkit-scrollbar-track {
    border-radius: 10px;
    background-color: #383838;
}
::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background-color: #6b6b6b;
}
::-webkit-scrollbar {
    width: 8px;
    background-color: #383838;
}


@media only screen and (max-width: 1281px) and (min-width: 992px) {
    .home_section{
        width:auto;
    }
}
@media(max-width:768px){
    .home_section{
        top: 25%;
        width:100%;
        left: 0;

        div{
           
            h1{
                font-size: 38px;
                line-height: 52px;
                text-align: center;
                margin-left: 0;
                .color-theme{
                    display: block;
                     margin-left: 0;
                     color: #037fff;
                     font-size: 48px;
                     background:unset!important;
                     -webkit-text-fill-color:#037fff!important;
                
                     
                }
            }
            p{
                font-size: 18px;
            }
        } 
    }
}